import React from 'react';
import ReactDOM from 'react-dom'
import loginstore from './stores/loginstore';
import './styles/loginform.css';
import axios from 'axios';

class LoginForm extends React.Component{
  constructor(props){
    super(props);

    this.handleForm = this.handleForm.bind(this);

    this.state = {
      localError: '',
      messageShowing: false
    };
  }

  componentDidUpdate (){
    if (this.props.isLogged){
      history.push('/');
    }
  }

  handleForm(e) {
    e.preventDefault();

    let uname = ReactDOM.findDOMNode(this.refs.unameField).value;
    let pwd = ReactDOM.findDOMNode(this.refs.pwdField).value;


    /* const loginAction = {
      type: 'LOGIN_REQ',
      username: uname,
      passwd: pwd
    }; */

    const options = {
      headers: {
        'Authorization': 'Basic ' + window.btoa(uname + ':' + pwd)
      }
    };
    axios.post("https://loginreactbackend.eu.pythonanywhere.com/login", {a:0}, options).then(resp => {
      return resp.data
    }).then(data => {
      if (data.logSuccess){
        const loginAction = {
          type: 'LOGIN_REQ',
          token: data.token,
          username: uname,
          logSuccess: data.logSuccess
        }

        loginstore.dispatch(loginAction);
        localStorage.removeItem('jwtToken');
        localStorage.setItem('jwtToken', data.token);
        localStorage.removeItem('uname');
        localStorage.setItem('uname', uname);

      } else {
        const loginAction = {
          type: 'LOGIN_REQ',
          token: '',
          username: '',
          logSuccess: false
        };
        loginstore.dispatch(loginAction);
        if (this.state.messageShowing){

          this.setState({localError: '', messageShowing: false});
          this.setState({localError: data.errorMsg, messageShowing: true});



        } else {
          this.setState({localError: data.errorMsg, messageShowing: true});
        }
      }

    });


  }

  render(){
    return(
      <div>
        {(this.state.localError)? <p className="errorMessage" ><span className="placeholding" ref="errorp">{this.state.localError}</span></p> : null}
        <form action="#" method="POST" className="myForm">
          <h2 className="formTitle">Login</h2>
          <input type="text" className="myField myUnameField" ref="unameField" />
          <input type="password" className="myField" ref="pwdField" />
          <button type="submit" className="mySubBtn" onClick={this.handleForm.bind(this)}>Log In</button>
        </form>
      </div>
    );
  }
}

function mapStateToProps(state){
  return {
    isLogged: state.isLogged
  };
}



export default LoginForm;



