import { createStore } from 'redux';

const initialState = {
  loginEnd: ''
};


const reducer = (state=initialState, action) => {
  switch(action.type){
    case 'LOGIN_REQ':


      if (action.logSuccess){
        let newData = {
          token: action.token,
          username: action.username,
          isLogged: true
        };
        return Object.assign({}, state,newData);

      } else {
        let newData = {
          token: '',
          username: '',
          isLogged: false
        };

        return Object.assign({}, state, newData);
      }




    case 'REGISTER_REQ':

      let newData = {
        token: (action.registerSuccess)? action.token: '',
        username:(action.registerSuccess)? action.username: '',
        isLogged: action.registerSuccess
      };
    case 'CHECK_LOGIN':

      newData = {
        token: action.token,
        username: action.username,
        isLogged: action.loginSuccess
      };

      if (action.loginSuccess){
        return Object.assign({},state,newData);
      } else {
        localStorage.removeItem('jwtToken');
        localStorage.removeItem('uname');


        return Object.assign({}, state, newData);

      }


    case 'LOGOUT':
      newData = {
        token: '',
        username: '',
        isLogged: false
      };

      return Object.assign({}, state, newData);

    default:
      return state;
  }
}


const loginstore = createStore(reducer)

export default loginstore;



