import React from 'react';
import './styles/navbar.css';
import LoginForm from './LoginForm';
import Register from './Register';
import MainPage from './MainPage';
import history from './history';
import { connect } from 'react-redux';
import loginstore from './stores/loginstore';
import {
  Router,
  Switch,
  Route,
  Link,
  Redirect
} from 'react-router-dom';

class NavigationBar extends React.Component {
  constructor(props){
    super(props);

    this.handleLogOut = this.handleLogOut.bind(this);
  }

  handleLogOut(){
    window.localStorage.removeItem('jwtToken');
    window.localStorage.removeItem('uname');

    const logoutAction = {
      type:'LOGOUT'
    };
    loginstore.dispatch(logoutAction);
  }

  render(){

    return (
      <Router history={history}>
      <div>
      <div className="navbar">
      <Link to="/login-react-frontend/" className="navbarbtn homebtn">Home</Link>
      <div className="navbarLeft">
      {(this.props.isLogged)? <Link to="/login-react-frontend/" className="navbarbtn" onClick={this.handleLogOut}>Log out</Link> :
      <div>
      <Link to="/login-react-frontend/login" className="navbarbtn">Login</Link><Link to="/login-react-frontend/register" className="navbarbtn">Register</Link>
      </div>
    }

    </div>
    </div>
    <Switch>
    <Route exact path="/login-react-frontend/">
    <MainPage store={loginstore}/>
    </Route>
    <Route path="/login-react-frontend/login">
    {(this.props.isLogged) ? <Redirect to="/login-react-frontend/" /> : <LoginForm store={loginstore}/> }
    </Route>
    <Route path="/login-react-frontend/register">
    {(this.props.isLogged) ? <Redirect to="/login-react-frontend/" /> : <Register store={loginstore} />}
    </Route>
    </Switch>
    </div>
    </Router>
  );
  }
}

function mapStateToProps(state){
  return {
    isLogged: state.isLogged
  };
}

export default connect(mapStateToProps)(NavigationBar);



