import React, { Component } from 'react';
import NavigationBar from './NavigationBar';
import './App.css';
import axios from 'axios';
import loginstore from './stores/loginstore';
import { connect } from 'react-redux';

class App extends Component {

  componentDidMount(){

    if (localStorage.getItem('jwtToken')){
      let token = localStorage.getItem('jwtToken');
      const customOptions = {
        headers: {
          'Authorization': 'Bearer ' + token
        }
      };

      axios.post('https://loginreactbackend.eu.pythonanywhere.com/auth', {a:5},customOptions).then(resp => {
        return resp.data;
      }).then(data => {
        if (data.loginSuccess){
          let logSuc;
          if (data.username === localStorage.getItem('uname')){
            logSuc = true;

          } else {
            logSuc = false;
          }
          const authAction = {
            type: 'CHECK_LOGIN',
            loginSuccess: logSuc,
            username: (logSuc)? data.username: '',
            token: (logSuc)? token: ''
          };

          loginstore.dispatch(authAction);

        }
      });

    } else {
      const authAction = {
        type: 'CHECK_LOGIN',
        loginSuccess: false
      };
      loginstore.dispatch(authAction);
    }

  }

  render() {
    return (
      <div className="App">
        <NavigationBar store={loginstore}/>
      </div>

    );
  }
}


function mapStateToProps(state){
  return {
    isLogged: state.isLogged
  };
}

export default connect(mapStateToProps)(App);



