import React from 'react';
import ReactDOM from 'react-dom';
import loginstore from './stores/loginstore';
import { connect } from 'react-redux';
import './styles/loginform.css';
import axios from 'axios';
import history from './history';

class Register extends React.Component {
  constructor(props){
    super(props);

    this.handleForm = this.handleForm.bind(this);

    this.state = {
      localError: '',
      messageShowing: false
    };

  }


  componentDidUpdate (){
    if (this.props.isLogged){
      history.push('/login-react-frontend/');
    }
  }

  handleForm(e){
    e.preventDefault();

    let rname = ReactDOM.findDOMNode(this.refs.realnameField).value;
    let uname = ReactDOM.findDOMNode(this.refs.unameField).value;
    let pwd = ReactDOM.findDOMNode(this.refs.pwdField).value;



    const reqoptions = {
      headers: {
        'Authorization': 'Basic ' + window.btoa(uname + ':' + pwd)
      }
    };



    axios.post("https://loginreactbackend.eu.pythonanywhere.com/register", {realName: rname}, reqoptions).then(resp => {
      return resp.data;
    }).then(data => {

      if (data.registerSuccess){

        const registerAction = {
          type: 'REGISTER_REQ',
          username: uname,
          token: data.token,
          registerSuccess: true
        };
          loginstore.dispatch(registerAction);
          localStorage.setItem('jwtToken', data.token);
          localStorage.setItem('uname', uname);
      } else {
        const registerAction = {
          type: 'REGISTER_REQ',
          username: '',
          token: '',
          registerSuccess: false
        };
        loginstore.dispatch(registerAction);

        if (this.state.messageShowing){

          this.setState({localError: '', messageShowing: false});
          this.setState({localError: data.errorMsg, messageShowing: true});



        } else {
          this.setState({localError: data.errorMsg, messageShowing: true});
        }
      }



    });


  }

  render(){
    return(
      <div>
      {(this.state.localError)? <p className="errorMessage"><span>{this.state.localError}</span></p>: null}
      <form action="#" method="POST" className="myForm">
        <h2 className="formTitle">Register</h2>
        <input type="text" className="myField myUnameField" ref="realnameField" />
        <input type="text" className="myField" ref="unameField" />
        <input type="password" className="myField" ref="pwdField" />
        <button type="submit" className="mySubBtn" onClick={this.handleForm.bind(this)}>Sign Up</button>
      </form>
      </div>
    );
  }
}

function mapStateToProps(state){
  return {
    isLogged: state.isLogged,
    errorMsg: state.errorMsg
  };
}


export default connect(mapStateToProps)(Register);



